import React from "react";

function QuestionPagination({
  items,
  setCurrentPage,
  setSelect,
  currentPage,
  setIncorrect,
  select,
}) {
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
    setSelect(false);
    setIncorrect(false);
  };
  const itemsPerPage = 1;
  const displayedPages = 3;
  const totalPages = Math.ceil(items?.length / itemsPerPage);

  const handleNext = () => {
    if (currentPage <= items.length - 1) {
      setSelect(false);
      setIncorrect(false);
      return setCurrentPage(currentPage + 1);
    }
  };
  const handlePre = () => {
    if (currentPage === 1) {
      return currentPage;
    } else {
      setSelect(false);
      setIncorrect(false);
      return setCurrentPage(currentPage - 1);
    }
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    let startPage = currentPage - Math.floor(displayedPages / 2);
    if (startPage < 1) {
      startPage = 1;
    }
    let endPage = startPage + displayedPages - 1;
    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = endPage - displayedPages + 1;
      if (startPage < 1) {
        startPage = 1;
      }
    }

    if (startPage > 1) {
      pageNumbers.push(
        <li key="left-dots pagination list">
          <button className="pagination border" disabled>
            &hellip;
          </button>
        </li>
      );
    }
    for (let i = startPage; i <= endPage; i++) {
      pageNumbers.push(
        <li key={i} className="ml-2 list pagination">
          <button
            disabled={select}
            onClick={() => handlePageChange(i)}
            className={
              i === currentPage ? "active pagination " : "pagination border"
            }
          >
            {i}
          </button>
        </li>
      );
    }
    if (endPage < totalPages) {
      pageNumbers.push(
        <li key="right-dots list pagination ml-5">
          <button className="pagination border ml-2" disabled>
            &hellip;
          </button>
        </li>
      );
    }
    return pageNumbers;
  };
  return (
    <div className="w-100 d-flex justify-content-center mt-5">
      <button
        onClick={() => handlePre()}
        className={`pagination font ml-1 border `}
      >
        Pre
      </button>
      <ul className="d-flex justify-content-center mx-2 list">
        {renderPageNumbers()}
      </ul>
      <button
        onClick={() => handleNext()}
        className={`pagination font ml-1 border `}
      >
        Next
      </button>
    </div>
  );
}

export default QuestionPagination;
