import Pagination from "react-bootstrap/Pagination";
import React from "react";

function Paginate({ handlePre, handleNext, page, pageCount, setPage }) {
  return (
    <>
      {page > 0 ? (
        <div>
          <Pagination className="ml-4 pl-lg-5 w-100">
            <Pagination.Prev onClick={() => handlePre()} />
            {Array(pageCount)
              .fill(null)
              .map((ele, i) => {
                return (
                  <div key={i}>
                    <Pagination.Item
                      className={
                        page === i + 1 ? "page-link-button ml-2" : "ml-2"
                      }
                      onClick={() => setPage(i + 1)}
                    >
                      {i + 1}
                    </Pagination.Item>
                  </div>
                );
              })}
            <Pagination.Next onClick={() => handleNext()} />
          </Pagination>
        </div>
      ) : null}
    </>
  );
}

export default Paginate;
