import React from "react";
import {
  FacebookShareButton,
  FacebookIcon,
  WhatsappShareButton,
  TwitterShareButton,
  TwitterIcon,
  WhatsappIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "react-share";
export default function Share() {
  const shareUrl = window.location.href;
  // const [url, setUrl] = useState(process.env.REACT_APP_FRONTEND_URL);
  // const handleShare = () => {
  //   setUrl("https://quizesweb.netlify.app/");
  // };
  return (
    <div className="share">
      <FacebookShareButton
        url={shareUrl}
        quote={"Happy Quiz"}
        hashtag="#quiz"
        // onClick={}
      >
        <FacebookIcon
          size={16}
          borderRadius={50}
          style={{ height: "100%", width: "50%" }}
        />
      </FacebookShareButton>
      <WhatsappShareButton
        url={shareUrl}
        //  onClick={handleShare}
      >
        <WhatsappIcon
          size={20}
          borderRadius={50}
          style={{ height: "100%", width: "50%" }}
        />
      </WhatsappShareButton>
      <TwitterShareButton
        url={shareUrl}
        //  onClick={handleShare}
      >
        <TwitterIcon
          size={20}
          borderRadius={50}
          style={{ height: "100%", width: "50%" }}
        />
      </TwitterShareButton>
      <LinkedinShareButton
        url={shareUrl}
        // onClick={handleShare}
      >
        <LinkedinIcon
          size={20}
          borderRadius={50}
          style={{ height: "100%", width: "50%" }}
        />
      </LinkedinShareButton>
    </div>
  );
}
