import React, { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import Swal from "sweetalert2";
import create from "../assests/create.svg";
import cancel from "../assests/cancel.svg";
import del from "../assests/del.svg";
import { quizUpdate, editQuizById } from "../Api";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import { useParams, useNavigate } from "react-router-dom";
import * as Yup from "yup";

const questionScehma = Yup.object().shape({
  quizTitle: Yup.string().required("Quiz title required"),
  quizdesc: Yup.string().required("Quiz description required"),
  questions: Yup.array().of(
    Yup.object().shape({
      question: Yup.string().required("Question required"),
      options: Yup.array().of(Yup.string().required("Option is required")),
      // .min(1, "At least one option is required"),
      correctAnswer: Yup.array()
        .of(Yup.string().required("Option is required"))
        .min(1, "At least one option is required"),
    })
  ),
});

export default function EditQuizScreen() {
  const [data, setData] = useState();
  const { id } = useParams();
  const user = JSON.parse(localStorage.getItem("user"));
  const navigate = useNavigate();
  useEffect(() => {
    const updatedQuiz = async () => {
      try {
        const res = await editQuizById(id);
        if (res) {
          setData(res?.data);
        } else {
          Swal.fire({
            width: "20em",
            height: "20em",
            position: "center",
            icon: "error",
            title: "Oops...",
            text: "Something went wrong!",
          });
        }
      } catch (error) {
        console.log(error?.message);
      }
    };
    updatedQuiz();
  });
  return (
    <Formik
      initialValues={{
        user_id: `${user?.userId}`,
        ...data,
      }}
      validationSchema={questionScehma}
      onSubmit={async (values) => {
        let res = await quizUpdate(id, values);
        if (res?.status === 200) {
          Swal.fire({
            width: "20em",
            height: "20em",
            position: "top-end",
            icon: "success",
            title: "Your work has been saved",
            showConfirmButton: false,
            timer: 1500,
          });
          navigate("/quanks/search");
        }
      }}
      enableReinitialize
    >
      {({ values, resetForm, errors, touched }) => (
        <Form>
          <div>
            <div className="d-flex justify-content-center align-items-center mx-3 ">
              <div className="quiz-title-card pb-2">
                <Field
                  type="text"
                  placeholder="Quiz Title"
                  name="quizTitle"
                  className={`quiz-card-heading quiz-card-input w-100 mt-2 ${
                    touched.quizTitle && errors.quizTitle ? "is-invalid" : ""
                  }`}
                />
                <ErrorMessage
                  component="div"
                  name="quizTitle"
                  className="invalid-feedback"
                />
                <hr />
                <Field
                  type="text"
                  placeholder="Quiz Description"
                  name="quizdesc"
                  className={`rofile-text px-0 pl-2 w-100 quiz-desc-input  ${
                    touched.quizdesc && errors.quizdesc ? "is-invalid " : ""
                  }`}
                />
                <ErrorMessage
                  component="div"
                  name="quizdesc"
                  className="invalid-feedback "
                />
              </div>
            </div>
            <FieldArray name="questions">
              {({ insert, remove, push }) => (
                <>
                  <Container className="w-100 d-flex justify-content-center aligin-items-center">
                    <div className=" mt-4 d-flex justify-content-start align-items-center create-container">
                      <button
                        type="button"
                        className="overlay-card-button p-0 create-quiz-button"
                        onClick={() =>
                          push({
                            question: "",
                            options: ["", "", "", ""],
                            correctAnswer: [],
                          })
                        }
                      >
                        Add a question
                      </button>
                      <button
                        type="button"
                        className="overlay-card-button px-1 create-quiz-button ml-3 "
                        onClick={() => remove({})}
                      >
                        Remove question
                      </button>
                    </div>
                  </Container>
                  <div className="px-3">
                    {values?.questions?.length > 0 &&
                      values?.questions?.map((question, index) => (
                        <div
                          className="w-100 d-flex justify-content-center aligin-items-center mt-2 "
                          key={index}
                        >
                          <div className="px-0 mt-2 create-question-card ">
                            <div className="d-flex mx-3 mt-2">
                              <h1
                                className="pt-2 "
                                style={{ color: "#454f59" }}
                              >
                                {index + 1}.
                              </h1>
                              <div className="mt-2 w-100">
                                <Field
                                  name={`questions.${index}.question`}
                                  placeholder="Untitled Question"
                                  type="text"
                                  className={`mt-2 create-question-card-heading quiz-card-input d-flex w-100 
                               ${
                                 touched.questions && errors.questions
                                   ? "is-invalid"
                                   : ""
                               }
                               `}
                                />
                                <ErrorMessage
                                  component="div"
                                  name={`questions.${index}.question`}
                                  className="invalid-feedback"
                                />
                              </div>
                            </div>
                            <hr className="mx-3" />
                            <FieldArray name={`questions.${index}.options`}>
                              {({ insert, remove, push, pop }) => (
                                <>
                                  <div key={index}>
                                    {values?.questions[index]?.options?.length >
                                      0 &&
                                      values?.questions[index]?.options?.map(
                                        (option, index2) => (
                                          <>
                                            <div
                                              className="row  w-100 ml-0 d-flex align-items-center"
                                              key={index2}
                                            >
                                              <div className="col mt-2 pl-3 w-100 d-flex align-items-center">
                                                <Field
                                                  type="checkbox"
                                                  name={`questions.${index}.correctAnswer`}
                                                  id={`questions.${index}.correctAnswer`}
                                                  value={`${values?.questions?.[index]?.options?.[index2]}`}
                                                  className={`radio ${
                                                    touched.questions &&
                                                    errors.questions
                                                      ? "is-invalid"
                                                      : ""
                                                  }`}
                                                />

                                                <Field
                                                  name={`questions.${index}.options.${index2}`}
                                                  type="text"
                                                  placeholder={`Enter Option${
                                                    index2 + 1
                                                  }`}
                                                  className={`w-75 overlay-card-para create-question pl-1  ${
                                                    touched.questions &&
                                                    errors.questions
                                                      ? "is-invalid border-red-500"
                                                      : ""
                                                  }`}
                                                />
                                                <ErrorMessage
                                                  component="div"
                                                  name={`questions.${index}.options.${index2}`}
                                                  className="invalid-feedback"
                                                />
                                                <ErrorMessage
                                                  component="div"
                                                  name={`questions.${index}.correctAnswer`}
                                                  className="invalid-feedback"
                                                />
                                              </div>
                                              {values?.questions[index]?.options
                                                .length >= 3 ? (
                                                <div className="pr-3">
                                                  <button
                                                    type="button"
                                                    className="delete-div"
                                                    onClick={() =>
                                                      remove(index2)
                                                    }
                                                  >
                                                    <img
                                                      src={del}
                                                      className="delete"
                                                      alt="del"
                                                    />
                                                  </button>
                                                </div>
                                              ) : null}
                                            </div>
                                          </>
                                        )
                                      )}
                                    {values?.questions[index]?.options.length <=
                                    3 ? (
                                      <div className=" mt-2 d-lg-flex d-md-flex d-sm-flex justify-content-between align-items-center pr-3">
                                        <button
                                          type="button"
                                          className="overlay-card-button px-1 create-quiz-button ml-4 mt-3 ml-lg-4"
                                          onClick={() => push("")}
                                        >
                                          Add Option
                                        </button>
                                      </div>
                                    ) : null}
                                  </div>
                                </>
                              )}
                            </FieldArray>
                          </div>
                        </div>
                      ))}
                  </div>
                </>
              )}
            </FieldArray>
          </div>
          <Container className=" d-flex justify-content-center aligin-items-center mt-5">
            <div className="d-flex justify-content-end align-items-center create-container">
              <button type="submit" className=" p-0 create-button">
                <div className="relative d-flex justify-content-center align-items-center create-button-div ">
                  <img src={create} alt="create mt-5" />
                  <p className="ml-1 create-button-text text-white pt-3">
                    Create
                  </p>
                </div>
              </button>
              <button
                onClick={() => {
                  resetForm({
                    values: {
                      user_id: "",
                      userName: "",
                      quizTitle: "",
                      quizdesc: "",
                      questions: [
                        {
                          question: "",
                          options: [
                            "Enter option1",
                            "Enter option2",
                            "Enter option3",
                            "Enter option4",
                          ],
                          correctAnswer: [],
                        },
                      ],
                    },
                  });
                }}
                type="submit"
                className=" p-0 ml-2 cancel-button"
              >
                <div className="relative d-flex justify-content-center align-items-center cancel-button-div">
                  <img src={cancel} alt="cancel" />
                  <p className="ml-2 create-button-text absolute text-white cancel-button-text">
                    Cancel
                  </p>
                </div>
              </button>
            </div>
          </Container>
        </Form>
      )}
    </Formik>
  );
}
