import React, { useEffect, useState } from "react";
import { Route, Routes } from "react-router-dom";
import "./App.css";
import "./bootstrap.min.css";
import QuizBar from "./components/QuizBar";
import Footer from "./components/Footer";
import {
  BankScreen,
  FavoriteBankScreen,
  HomeScreen,
  LoginScreen,
  ProfileScreen,
  QuestionScreen,
  QuizScreen,
  SearchScreen,
  SignupScreen,
  EditQuizScreen,
  PlayScreen,
} from "./screens";
import ProtectedRouted from "./components/ProtectedRoutes";
import ErrorMessage from "./components/ErrorMessage";
import { UserContextProvider } from "./context";
import ProfileCard from "./components/setting/ProfileCard";
import BasicInfo from "./components/setting/BasicInfo";
import Password from "./components/setting/Password";

function App() {
  const [value, setValue] = useState(0);
  useEffect(() => {}, [value]);

  return (
    <UserContextProvider>
      <QuizBar value={value} setValue={setValue} />

      <Routes>
        <Route path="/" element={<HomeScreen />} />
        <Route path="/quanks/login" element={<LoginScreen />} />
        <Route path="/quanks/signup" element={<SignupScreen />} />
        <Route path="/quanks/search" element={<SearchScreen />} />
        <Route path="/quanks/play" element={<PlayScreen />} />
        <Route
          path="/quanks/play/:id"
          element={<QuestionScreen value={value} setValue={setValue} />}
        />
        <Route path="/" element={<ProtectedRouted />}>
          <Route path="/quanks/create" element={<QuizScreen />} />
          <Route path="/quanks/create/:id/edit" element={<EditQuizScreen />} />
          <Route path="quanks/setting" element={<ProfileScreen />}>
            <Route index element={<ProfileCard />} />
            <Route path="profile/:id" element={<ProfileCard />} />
            <Route path="basicinfo/:id" element={<BasicInfo />} />
            <Route path="password/:id" element={<Password />} />
          </Route>
          <Route path="/quanks/bank/:id" element={<BankScreen />} />
          <Route path="/quanks/favBank/:id" element={<FavoriteBankScreen />} />
        </Route>
        <Route path="*" element={<ErrorMessage />} />
      </Routes>
      <Footer />
    </UserContextProvider>
  );
}

export default App;
