import React, { useState } from "react";
import Table from "react-bootstrap/Table";
import trash from "../assests/ic_Trash.svg";
import vector from "../assests/vector3.svg";
import play from "../assests/playicon.svg";
import edit from "../assests/ic_Write_message.svg";
import dots from "../assests/threedots.svg";
import { Link } from "react-router-dom";
import ShareModal from "../components/ShareModal";
import { deleteMyBanks } from "../Api";
import Paginate from "./Paginate";
import Swal from "sweetalert2";

function BankTable({ data, handlePre, handleNext, page, pageCount, setPage }) {
  const [share, setShare] = useState(false);
  const handleDelete = async (id, quizId) => {
    const res = await deleteMyBanks(id, quizId);
    if (res) {
      Swal.fire({
        width: "20em",
        height: "20em",
        position: "top-end",
        icon: "success",
        title: "Quiz delated successfully",
        showConfirmButton: false,
        timer: 1500,
      });
    }
  };
  return (
    <>
      <div className="wrapper ">
        <Table striped bordered hover>
          <thead>
            <tr className="p-lg-0">
              <th scope="col" className="nav-subheading text-capitalize">
                Title
              </th>
              <th scope="col" className="nav-subheading text-capitalize">
                No, of Questions
              </th>
              <th scope="col" className="nav-subheading text-capitalize">
                Create On
              </th>
              <th scope="col" className="nav-subheading text-capitalize">
                Action
              </th>
            </tr>
          </thead>
          <tbody className="border-0">
            {data?.quizByUser?.map((obj, i) => (
              <tr key={obj?._id}>
                <td data-label="Title" className="choose-file-desc ">
                  {obj?.quizTitle}
                </td>
                <td
                  data-label="No, of Questions"
                  className="choose-file-desc mt-lg-0 mt-md-0 mt-sm-0"
                >
                  {obj?.questions?.length}
                </td>
                <td data-label="Create On" className="choose-file-desc">
                  {obj?.timestamp.toString().split("", 10)}
                </td>
                <td
                  data-label="Action "
                  className="choose-file-desc d-flex justify-content-center align-items-center "
                >
                  <div className="w-50 d-flex justify-content-between align-items-center  ml-5">
                    <Link to={`/quanks/play/${obj?._id}`}>
                      <img alt="play" src={play} className="bank-icons" />
                    </Link>
                    <Link to={`/quanks/create/${obj?._id}/edit`}>
                      <img alt="edit" src={edit} className="bank-icons" />
                    </Link>
                    <img
                      alt="share"
                      src={vector}
                      onClick={() => setShare(true)}
                      className="bank-icons"
                    />
                    <img
                      alt="delete"
                      src={trash}
                      className="bank-icons"
                      onClick={() => handleDelete(obj?.user_id, obj?._id)}
                    />

                    <img alt="more" src={dots} className="bank-icons" />
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
      {pageCount !== 1 ? (
        <div className="d-lg-flex justify-content-between align-items-center w-75 ml-lg-0 ml-5">
          <p>
            Showing {page} of {pageCount} pages
          </p>
          <Paginate
            handleNext={handleNext}
            handlePre={handlePre}
            page={page}
            pageCount={pageCount}
            setPage={setPage}
            className="w-100 "
          />
        </div>
      ) : null}

      <ShareModal share={share} setShare={setShare} />
    </>
  );
}

export default BankTable;
