import React, { useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";

export default function PlayScreen() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <>
      <Container fluid="sm">
        <Row>
          <Col className="d-flex justify-content-center align-items-center rounded-sm">
            <div className="border question-card  h-100  d-flex justify-content-center align-items-center flex-column px-5">
              <h3 className="text-capitalize text-color text-center">
                Don't have quiz to play...
              </h3>
              <h3 className="text-capitalize text-color text-center">
                Lets get started
              </h3>
              <Link to="/quanks/create" className="w-100">
                <button className="w-100 signin-button w-100 mt-2 text-capitalize">
                  Create Quiz
                </button>
              </Link>
              <Link to="/quanks/search" className="w-100">
                <button className="signin-button w-100 mt-2 text-capitalize">
                  Search Quiz
                </button>
              </Link>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
}
