import React, { useState } from "react";
import editIcon from "../../assests/edit-profile.svg";
import { profileChange } from "../../Api";
import { useParams, useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { useUserContext } from "../../context";
import image from "../.././assests/Vector.svg";

export default function ProfileCard() {
  const { id } = useParams();
  const { user } = useUserContext();
  const [previewImage, setPreviewImage] = useState(null);
  const [filename, setImage] = useState();
  const [data, setData] = useState({
    userName: user?.userName ?? "",
    city: user?.city ?? "",
    country: user?.country ?? "",
    bio: user?.bio ?? "",
  });
  const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handlefile = (e) => {
    setImage(e.target.files[0]);
    setPreviewImage(URL.createObjectURL(e.target.files[0]));
  };

  const handleSave = async (e) => {
    e.preventDefault();
    const { userName, city, country, bio } = data;
    if (filename || userName || city || country || bio) {
      var formData = new FormData();
      formData.append("userName", userName);
      if (!!filename) formData.append("filename", filename);
      formData.append("city", city);
      formData.append("country", country);
      formData.append("bio", bio);
      const res = await profileChange(id, formData);
      const userData = {
        ...JSON.parse(localStorage.getItem("user")),
        userName: res?.data?.user?.userName,
        filename: res?.data?.user?.filename,
        city: res?.data?.user?.city,
        country: res?.data?.user?.country,
        bio: res?.data?.user?.bio,
      };
      if (res?.data?.user?.filename) {
        setImage(res.data.user.filename);
      }
      if (res) {
        localStorage.setItem("user", JSON.stringify(userData));
        Swal.fire({
          width: "20em",
          height: "20em",
          position: "top-end",
          icon: "success",
          title: "changes saved successfully",
          showConfirmButton: false,
          timer: 1500,
        });
      }
    }
  };
  const navigate = useNavigate();

  return (
    <form
      encType="multipart/form-data"
      action="/upload"
      className="profile-card border d-flex justify-content-center mt-5"
      onMouseOver={() => {
        if (id !== user?.userId) navigate(`/quanks/setting/profile/${user?.userId}`);
      }}
    >
      <div className="profile-card-inner-div ">
        <h1 className="card-text text-capitalize pt-3">Your Photo</h1>
        <div className="d-flex mt-4 ">
          <div>
            {user?.filename || previewImage ? (
              <img
                src={
                  previewImage ??
                  `${process.env.REACT_APP_IMAGE_BASE_URL}/${user?.filename}`
                }
                alt="Profile"
                className="profile-image-card "
              />
            ) : (
              <img src={image} alt="Profile" className="profile-image-card " />
            )}

            <img src={editIcon} alt="edit" className=" profile-edit" />
          </div>
          <div className="pl-3 choose-image">
            <input
              type="file"
              id="profile"
              name="filename"
              multiple
              onChange={handlefile}
              accept="image/*"
            />
            <label htmlFor="profile" className="pt-1 choose-image">
              JPG, PNG or GIF, 150X150 min image will be resized if larger
            </label>
          </div>
        </div>
        <div className=" py-3">
          <div className="d-flex justify-content-between align-items-center mt-2">
            <label className="profile-label">Username</label>
            <input
              type="text"
              onChange={handleChange}
              name="userName"
              className="w-75 profile-input p-2"
              defaultValue={user?.userName}
            />
          </div>
          <div className="d-flex justify-content-between align-items-center mt-3">
            <label className="profile-label">City</label>
            <input
              // value={data?.city}
              defaultValue={user?.city}
              className="w-75 profile-input p-2"
              onChange={handleChange}
              name="city"
            />
          </div>
          <div className="d-flex justify-content-between align-items-center mt-3">
            <label className="profile-label">Country</label>
            <input
              onChange={handleChange}
              name="country"
              className="w-75 profile-input p-2"
              defaultValue={user?.country}
            />
          </div>
          <div className="d-flex  mt-3 flex-column ">
            <div className="d-flex justify-content-between align-items-start mt-1 w-100">
              <label className="profile-label">Bio</label>
              <textarea
                rows="4"
                maxLength="250"
                onChange={handleChange}
                className="w-75 profile-textarea p-2"
                name="bio"
                defaultValue={user?.bio}
              />
            </div>
            <p className="search-quiz-card-para text-center w-50 ml-5 pl-4 mt-1">
              {data.bio.length}/250 characters
            </p>
          </div>
          <div className="d-flex justify-content-end align-items-center">
            <button onClick={handleSave} className="profile-save-button">
              <span className=" ">Save Change</span>
            </button>
          </div>
        </div>
      </div>
    </form>
  );
}
