import React, { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import SubjectCard from "../components/SubjectCard";
import { PlayQuizById } from "../Api";
import Swal from "sweetalert2";
import Loader from "../components/Loader";
import QuestionPagination from "../components/QuestionPagination";
import { useLocation } from "react-router-dom";

export default function QuestionScreen({ value, setValue }) {
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(true);
  const [select, setSelect] = useState(false);
  const [click, setClick] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [currentRound, setCurrentRound] = useState(1);
  const [currentQuestion, setCurrentQuestion] = useState([]);
  const [changeBg, setChangeBg] = useState();
  const [incorrect, setIncorrect] = useState(false);
  const [data, setData] = useState();
  const itemsPerPage = 1;
  const location = useLocation();
  const { id } = useParams();
  useEffect(() => {
    window.scrollTo(0, 0);
    getData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    return localStorage.setItem(
      "userplayquiz",
      JSON.stringify(location.pathname.split("/")[3])
    );
    // console.log(JSON.stringify(location.pathname.split("/")[2]), "ply");
    // eslint-disable-next-line react-hooks/exhaustive-deps
  });

  const getData = async () => {
    try {
      const res = await PlayQuizById(id);
      if (res) {
        setLoading(false);
        const newArray = [...res?.data?.questions];
        for (let i = newArray.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [newArray[i], newArray[j]] = [newArray[j], newArray[i]];

          for (let index = 0; index < newArray[i].options?.length; index++) {
            const j = Math.floor(Math.random() * (index + 1));
            [newArray[i].options[index], newArray[i].options[j]] = [
              newArray[i].options[j],
              newArray[i].options[index],
            ];
          }
        }
        setData({
          ...res?.data,
          questions: [...newArray],
        });
        setCurrentQuestion(newArray.slice(0, 10));
      } else {
        setLoading(false);
      }
    } catch (error) {
      Swal.fire({
        width: "20em",
        height: "20em",
        position: "top-end",
        icon: "error",
        title: "Oops...",
        text: "Something went wrong!",
      });
    }
  };
  const handleAnswerOptionClick = (isCorrect, ele) => {
    const equal = isCorrect?.filter((val, i) => {
      return ele === val;
    });

    if (equal[0] === ele && select === false) {
      setIncorrect(true);
      setValue(value + 1);
      setSelect(true);
      localStorage.setItem("scores", JSON.stringify(value + 1));
    } else {
      setSelect(true);
      setIncorrect(false);
      localStorage.setItem("scores", JSON.stringify(value));
    }

    setTimeout(() => {
      if (currentPage === currentQuestion.length) {
        setChangeBg(null);
        setIncorrect(null);
        return currentPage;
      } else {
        setCurrentPage(currentPage + 1);
        setSelect(false);
        setChangeBg(null);
        setIncorrect(false);
      }
    }, 2000);
  };
  const handleNextRound = () => {
    if (
      currentRound <= Math.floor(currentQuestion.length / currentRound) &&
      currentQuestion.length >= 10
    ) {
      setCurrentRound(currentRound + 1);
      setCurrentQuestion(
        data?.questions?.slice(currentRound * 10, (currentRound + 1) * 10)
      );
      setCurrentPage(1);
      setClick(false);
    } else if (currentQuestion.length < 10) {
      setClick(true);
      return currentQuestion;
    } else {
      setClick(true);
    }
  };
  const lastIndex = currentPage * itemsPerPage;
  const firstIndex = lastIndex - itemsPerPage;
  const currentQuestions = currentQuestion.slice(firstIndex, lastIndex);
  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        <>
          {data && currentQuestions.length > 0 ? (
            <>
              <Container fluid="sm">
                <Row className="d-flex justify-content-center mt-5">
                  <button
                    className="round-button"
                    disabled={click}
                    onClick={() => handleNextRound()}
                  >
                    Next Round
                  </button>
                </Row>

                <Row>
                  {currentQuestions?.map((obj, i) => (
                    <Col
                      key={obj?._id}
                      className="d-flex justify-content-center align-items-center rounded-sm"
                    >
                      <div className="border question-card pb-1">
                        <div className="card-header d-flex justify-content-between align-items-center">
                          <p className="card-text">Question</p>
                          <p className="card-text">Round {currentRound}</p>
                        </div>
                        <div className="d-flex flex-column w-100 align-items-center pt-3 card-inner">
                          <h1 className="card-text text-center">
                            {obj?.question}
                          </h1>
                          {obj?.options?.map((ele, id) => (
                            <button
                              disabled={select}
                              key={id}
                              className={`card-button ${
                                changeBg === id && incorrect
                                  ? "active"
                                  : changeBg === id && !incorrect
                                  ? "inactive"
                                  : ""
                              }`}
                              onClick={() => {
                                handleAnswerOptionClick(
                                  obj?.correctAnswer,
                                  ele
                                );
                                setChangeBg(id);
                              }}
                            >
                              {ele}
                            </button>
                          ))}
                        </div>
                      </div>
                    </Col>
                  ))}
                </Row>
              </Container>

              <QuestionPagination
                items={currentQuestion}
                setCurrentPage={setCurrentPage}
                currentPage={currentPage}
                setSelect={setSelect}
                setIncorrect={setIncorrect}
                select={select}
              />

              <SubjectCard
                numberOfQuestions={data?.questions?.length}
                title={data?.quizTitle}
                score={value}
                show={show}
                setShow={setShow}
              />
            </>
          ) : (
            <Container fluid="sm">
              <Row>
                <Col className="d-flex justify-content-center align-items-center rounded-sm">
                  <div className="border question-card  h-100  d-flex justify-content-center align-items-center flex-column px-5">
                    <h2 className="text-capitalize text-color text-center">
                      Sorry Quiz no longer exits...
                    </h2>
                    <h3 className="text-capitalize text-color text-center">
                      Lets get started
                    </h3>
                    <Link to="/quanks/create" className="w-100">
                      <button className="w-100 signin-button w-100 mt-2 text-capitalize">
                        Create Quiz
                      </button>
                    </Link>
                    <Link to="/quanks/search" className="w-100">
                      <button className="signin-button w-100 mt-2 text-capitalize">
                        Search Quiz
                      </button>
                    </Link>
                  </div>
                </Col>
              </Row>
            </Container>
          )}
        </>
      )}
    </>
  );
}
