import React, { useEffect, useState } from "react";
import { Container, Row, Col, Overlay } from "react-bootstrap";
import { Mybanks, getAllFavQuiz, createCombineQuiz } from "../Api";
import { useUserContext } from "../context";
import { useNavigate } from "react-router-dom";

function QuestionPopup({ show, setShow }) {
  const [mybanks, setMybanks] = useState();
  const [favbanks, setFavbanks] = useState();
  const [ids, setIds] = useState([]);
  const [favIds, setFavIds] = useState([]);

  const { user } = useUserContext();
  const navigate = useNavigate();

  useEffect(() => {
    getMyBank();
    getFavBank();
    // eslint-disable-next-line
  }, []);

  const getMyBank = async () => {
    const res = await Mybanks(user?.userId);
    setMybanks(res?.data?.quizByUser);
  };

  const getFavBank = async () => {
    const res = await getAllFavQuiz(user?.userId);
    setFavbanks(res?.data?.quizByUser);
  };

  const handleChange = (e) => {
    const value = e.target.value;
    const check = e.target.checked;
    if (check) {
      setIds([...ids, value]);
    } else {
      setIds(ids?.filter((e) => e !== value));
    }
  };

  const handleFavChange = (e) => {
    const value = e.target.value;
    const check = e.target.checked;
    if (check) {
      setFavIds([...favIds, value]);
    } else {
      setFavIds(favIds?.filter((e) => e !== value));
    }
  };

  const data = [...ids, ...favIds];
  const result = Array.from(new Set(data));
  const handleSubmit = async () => {
    const res = await createCombineQuiz(user?.userId, result);
    if (res) {
      navigate(`/quanks/play/${res?.data?.quiz[0]?._id}`);
      window.location.reload(false);
      setShow(false);
    }
  };
  return (
    <>
      <Overlay show={show} placement="right">
        {({ arrowProps }) => (
          <div
            style={{
              height: "100vh",
              width: "100%",
              position: "fixed",
              top: "0%",
              left: "0%",
              backgroundColor: "rgba(0, 0, 0, 0.2)",
              padding: "2px 10px",
              color: "white",
              borderRadius: 3,
              zIndex: 5,
            }}
            className="d-flex justify-content-center align-items-center"
          >
            <Container fluid="sm">
              <Row>
                <Col className="d-flex justify-content-center align-items-center rounded-sm">
                  <div className="border overlay-card bg-white ">
                    <div className="overlay-card-header d-flex justify-content-center align-items-center">
                      <p className="card-text text-right mt-3 ml-5 w-50">
                        Chose
                      </p>
                      <div className="w-50 d-flex justify-content-end align-items-center">
                        <div
                          className="overlay-card-close "
                          onClick={() => setShow(false)}
                        >
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="16"
                            height="16"
                            fill="currentColor"
                            className="bi bi-x-lg close"
                            viewBox="0 0 16 16"
                          >
                            <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z" />
                          </svg>
                        </div>
                      </div>
                    </div>
                    <div className="w-100 pt-1 card-inner">
                      <h1 className="overlay-card-heading">My Banks</h1>
                      {mybanks?.length > 0 ? (
                        <>
                          {mybanks?.map((item, index) => {
                            if (index === 0 || index === 1) {
                              return (
                                <div key={item?._id}>
                                  <label className="d-flex ml-4 pl-1 mt-1">
                                    <input
                                      type="checkbox"
                                      value={`${item?._id}`}
                                      onChange={(e) =>
                                        handleChange(e, item?._id)
                                      }
                                      name={`${item?._id}`}
                                    />
                                    <span className="cb-icon mt-1"></span>
                                    <span className="text-dark ml-2 overlay-card-para">
                                      {item?.quizTitle}
                                    </span>
                                  </label>
                                </div>
                              );
                            }
                            return null;
                          })}
                        </>
                      ) : (
                        <p className="text-dark ml-2 overlay-card-para">
                          No Favourite Banks to show
                        </p>
                      )}
                    </div>
                    <div className="w-100 card-inner">
                      <h1 className="overlay-card-heading">Favourite Banks</h1>
                      {favbanks?.length > 0 && favbanks ? (
                        <>
                          {favbanks?.map((item, index) => {
                            if (index === 0 || index === 1) {
                              return (
                                <div key={item?._id}>
                                  <label className="d-flex ml-4 pl-1 mt-1">
                                    <input
                                      type="checkbox"
                                      name={`${item?.quiz_id}`}
                                      value={`${item?.quiz_id}`}
                                      onChange={(e) => handleFavChange(e)}
                                    />
                                    <span className="cb-icon mt-1"></span>
                                    <span className="text-dark ml-2 overlay-card-para">
                                      {item?.title}
                                    </span>
                                  </label>
                                </div>
                              );
                            }
                            return null;
                          })}
                        </>
                      ) : (
                        <p className="text-dark ml-2 overlay-card-para">
                          No Favourite Banks to show
                        </p>
                      )}
                    </div>
                    <div className="w-100 mt-4 d-flex justify-content-center align-items-center">
                      <button
                        type="submit"
                        onClick={() => handleSubmit()}
                        className="overlay-card-button text-capitalize"
                      >
                        Save
                      </button>
                    </div>
                  </div>
                </Col>
              </Row>
            </Container>
          </div>
        )}
      </Overlay>
    </>
  );
}

export default QuestionPopup;
